.quotation_form{
    position: relative;
    width:95%;
    left:20px;
    top:100px;
}

.addColBtn{
    position: relative;
    right:-100%;
    cursor: pointer;
}

textarea{
    resize: none;
}

.invalidOrderNumMsg{
    position: absolute;
    top:40px;
    left:230px;
    color:red;
}

.invalidItemFormMsg{
    position: absolute;
    top:210px;
    left:150px;
    color:red;
}

.uploadConfirmPrompt{
    position: absolute;
    top:150px;
    height: 220px;
    max-width: 50%;
    width: 50%;
    z-index: 10;
    opacity: 1;
    left: 25%;
}

.confirmPromptWrapper{
    position: absolute;
    top: 0px;
    height:100%;
    width:100%;
    z-index: 10;
}

.uploadSuccessMsg{
    color: red;
}