html, body{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    overflow: hidden;
    margin: 0;
    padding:0;
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background-color: #dee6ed;
}

.MainWrapper{
    position: absolute;
    left:15%;
    top:80px;
    width:85%;
    height:90%;
    background-color:#dee6ed;
    overflow-y: scroll;
}

@media only screen and (max-width: 1350px) {
    .MainWrapper{width:85%;left:15%;}
    
}


.editBtnWrapper{
    position: absolute;
    top:65px;
    right:10px;
}

.editIcon {
    position: relative;
    top:0px;
    right:0px;
    font-size:30px;
    cursor: pointer;
}

.editTag{
    position: relative;
    top:-65px;
    padding-left:10px;
    padding-right:10px;
    right:10px;
    font-size:18px;
    background-color:rgba(69, 77, 85, 0.3);
    color:black;
    border-radius: 15%;
    opacity:0;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.editIcon:hover + .editTag{
    opacity:1;
}


.refreshBtnWrapper{
    position: absolute;
    top:65px;
    right:10px;
}

.refreshIcon {
    position: relative;
    top:0px;
    right:0px;
    font-size:30px;
    cursor: pointer;
}

.refreshTag{
    position: relative;
    top:-65px;
    padding-left:10px;
    padding-right:10px;
    right:10px;
    font-size:18px;
    background-color:rgba(69, 77, 85, 0.3);
    color:black;
    border-radius: 15%;
    opacity:0;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.refreshIcon:hover + .refreshTag{
    opacity:1;
}
.wrapper{
    position: absolute;
    top:0px;
    left:0px;
    margin: 0;
    padding: 0;
    height:100%;
    width: 100%;
    background-color: white;
}

.container{
    width: 500px;
    height: 450px;
    text-align: center;
    background-color: rgb(0, 51, 153, .5);
    border-radius: 4px;
    margin: 0 auto;
    margin-top: 150px;
}

.container img{
    width: 50%;
    height: 25%;
    position: relative;
    top:-90px;
    left:-55px;
}

.title{
    position: relative;
    top:-50px;
    font-size:25px;
    color:white;
}

input{
    height: 50px;
    width: auto;
    font-size: 18px;
    border: none;
    margin-bottom: 20px;
    background-color: #ffffff;
    padding-left: 40px;
}

.form-input::before{
    content: "\F007";
    position: absolute;
    font-family: "FontAwesome";
    padding-left: 5px;
    color: rgb(0, 0, 0, .2);
    padding-top: 7px;
    font-size: 35px;
}



.form-input:nth-child(2)::before{
    content: "\F023";
}

.btn-login{
    padding: 15px 25px;
    cursor: pointer;
    color: #fff;
    border-radius: 4px;
    border: none;
    background-color: rgb(0, 51, 153);
    border-bottom: 4px solid #002b80;
}

.errorMessage{
    position: relative;
    top:25px;
    color:#cc3300;
}
.navbar{
    position: absolute;
    width:15%;
    height:100%;
    padding:0px;
    background-color: #263544;
}

@media only screen and (max-width: 1350px) {
    .navbar{width: 15%;}
}

.navbar-nav{
    position: absolute;
    top:70px;
}

.nav-item{
    margin-top:20px;
    margin-bottom:20px;
    cursor: pointer;
    height:20px;
}

.nav-link{
    color:#b7c0cd;
    margin-left:35px;
}

.sperator{
    position: absolute;
    top:80px;
    width: 100%;
    height:1px;
    left:0px;
    background-color: gray;
}

.navIcons{
    position: relative;
    left:10px;
    top:40px;
    color:#b7c0cd;
}

.userIcon{
    position: absolute;
    top:20px;
    left:20px;
    font-size:40px;
    color:#b7c0cd;
}

.logoutIcon{
    position:relative;
    top:0px;
}

.logoutTag{
    position: relative;
    top:-30px;
    left:30px;
}

.logoutWrapper{
    position: absolute;
    bottom:0px;
    left:20px;
    font-size: 20px;
    color:rgba(255,255,255,.5);
    cursor: pointer;
}

.logoutWrapper:hover {
    color:rgba(255,255,255,1);
}

.username{
    position: absolute;
    top:30px;
    left:70px;
    color:rgba(255,255,255,.5);

}

.Wrapper{
    cursor: pointer;
}

.Wrapper:hover .userIcon{
    color:rgba(255,255,255,1);
}

.Wrapper:hover .username{
    color:rgba(255,255,255,1);
}

.underline{
    position: relative;
    left:5px;
    top:0px;
    width:0;
    height:1px;
    background-color: white;
    -webkit-transition:0.4s;
    transition:0.4s;
}

.nav-item:hover .underline{
    width:100%;
}

.nav-item:hover .navIcons, .nav-item:hover .nav-link{
    color:rgba(255,255,255,1);
}
.orderTable{
    position: absolute;
