.detailsTable{
    font-size:16px;
}

.Order_editor{
    position: absolute;
    width:100%;
    height:600px;
    top:100px;
    background-color: #dee6ed;
    padding:10px;
}

.closeIcon{
    position: absolute;
    top:10px;
    right:10px;
    font-size: 25px;
    cursor: pointer;
    z-index: 10;
}

.msg{
    position: absolute;
    top:200px;
    display: inline-block;
}

.updateBtn{
    position: absolute;
    top:150px;
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    cursor:pointer;
}

.comfirmPrompt{
    position: absolute;
    height:220px;
    max-width: 50%;
    width: 50%;
    z-index: 10;
    opacity:1;
    left:25%;
}

.confirmBtn{
    position: relative;
    top:10px;
    left:10px;
}

.updateLog{
    font-size:16px;
    position: absolute;
    top:550px;
    left:10px;
    width:100%;
    background-color: white;
    max-height:150px;
    overflow-y: scroll;
}

.updateLogTag{
    position: absolute;
    top:500px;
}